* {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.main{
  border:1px solid #50625b;
  background:black;
  color: white;
  min-width: 450px;
  min-height: 620px;
  grid-gap: 30px;
  font-size: 19px;
  display:grid;
  grid-template-columns:repeat(4, 1fr);
  padding: 30px;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 3px 6px 40px white;
  margin-bottom: 10px;
}

.main-header{
  display: flex;
  background-color: #0d0d0d;
  border-radius: 10px;
  margin-bottom: 2px;
}

.main-header img{
  border-radius: 50%;
  max-width: 80px;
  max-height: 80px;
  padding: 5px;
}

.main-header .post-info {
  text-align: left;
  margin-left: 10px;
  padding: 10px 10px;
}

.post-info h4{
  font-size: 20px;
  margin-bottom: 5px;
  color: rgba(255, 255, 255, 0.89);
}

.post-info p{
  font-size: 12px;
  color: #535f5a;
}

.main-content{
  background-color: #0d0d0d;
  border-radius: 10px;
}
.main-content .caption{
  font-size: 16px;
  color: rgba(255, 255, 255, 0.89);
  padding-top: 10px;
  margin-bottom: -10px;
}

.footer{
  margin-top: 12px;
  padding: 0 40px;
  display: flex;
  justify-content: space-between;
}

.footer button{
  padding: 9px;
  height:40px ;
  font-size: 18px;
  border-radius: 5px;
  border:0;
  font-weight:600;
  background-color:#808080;
  cursor: pointer;
  color: black;
  transition: 1s all;
}

.footer button:hover{
  background-color: rgb(27, 26, 26);
  color:rgba(255, 255, 255, 0.89) ;
}

.data-content{
  padding: 20px;
  border: 1px solid red;
}


.data-content .dlt ,.edit{
  background-color: silver;
  text-transform: uppercase;
  padding: 13px;
  height:45px ;
  margin-right: 4px;
  font-size: 20px;
  border-radius: 10px;
  border:0;
  font-weight:600;
  /* background-color:#c6b9b9; */
  cursor: pointer;
}